.section.intro-text {
    background: $light_gray;
    text-align: center;
}
.section.intro-text::after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -26px;
    border-width: 26px 26px 0 26px;
    border-color: $light_gray transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: ' ';
    z-index: 2;
}
.section.intro-text .title,
    .section.intro-text .body,
    .section.intro-text .button {
    opacity: 0;
}
.section.intro-text .container {
    padding: 50px 15px;
}
@media (min-width: $(desktop)) {
    .section.intro-text .container {
        padding: 100px 0;
    }
}
@media (min-width: $(desktop)) {
    .section.intro-text .text {
        margin-left: calc(99.99% * (-1/12 * -1) - (30px - 30px * (-1/12 * -1)) + 30px) !important;
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 10/12 - (30px - 30px * 10/12));
    }
    .section.intro-text .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.intro-text .text:last-child {
        margin-right: 0;
    }
    .section.intro-text .text:nth-child(12 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.intro-text .text img {
    max-width: 100%;
}
.section.intro-text h2 {
    color: $blue;
    margin-bottom: 25px;
    text-transform: capitalize;
    font-size: 24px;
}
@media (min-width: $(desktop)) {
    .section.intro-text h2 {
        line-height: 68px;
        font-size: 48px;
    }
}
.section.intro-text .body {
    font-family: 'Merriweather';
    line-height: 30px;
    font-size: 18px;
}
@media (min-width: $(desktop)) {
    .section.intro-text .body {
        padding: 0 150px;
    }
}
